methods: {
async SaveRegistInfo() {
let that = this;

try {
let reqData = this.userInfo;
/**/
let userBase = JSON.parse(localStorage.getItem("useBaseInfo")) ;
let weixinData = {};
weixinData.openid = localStorage.getItem("openid"); 

weixinData.nickname = userBase.nickname;
weixinData.province = userBase.province;
weixinData.city = userBase.city;
weixinData.headimgurl = userBase.headimgurl;
weixinData.country = userBase.country;

reqData.nation = this.$refs["nation"].getFullValue()[0].value;
reqData.openid = localStorage.getItem("openid");

if (reqData.certNo == "") {
this.$vux.toast.show({ text: "", type: "text" });
return;
}
if (!myUtils.isIdCard(reqData.certNo)) {
this.$vux.toast.show({ text: "", type: "text" });
return;
}
if (reqData.name == "") {
this.$vux.toast.show({ text: "", type: "text" });
return;
}
if (reqData.phone == "") {
this.$vux.toast.show({ text: "", type: "text" });
return;
}
let List = localStorage.getItem("userList");
if (myUtils.listIsExist(List, reqData.certNo, "certNo")) {
this.$vux.toast.show({
text: ""+reqData.certNo+"",
type: "text"
});
return;
}
reqData.birth = this.getBirthdayFromIdCard(reqData.certNo);
reqData.wechatCode = this.wechatCode;
this.$vux.loading.show({ text: "Loading" }); 
let data = { cardInfo: reqData, wixin: weixinData };
const { getH1001 } = require("../../static/wxGetData");
let res = await getH1001(data).catch(error => {
throw new Error(error.retMsg);
});
this.$vux.loading.hide(); 

this.$router.replace({ name: "myInfo" });
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.toast.show({ text: error.message, type: "text" });
} finally {
this.$vux.loading.hide(); 
}
},
getBirthdayFromIdCard(idCard) {
var birthday = "";
if (idCard != null && idCard != "") {
if (idCard.length == 15) {
birthday = "19" + idCard.substr(6, 6);
} else if (idCard.length == 18) {
birthday = idCard.substr(6, 8);
}

birthday = birthday.replace(/(.{4})(.{2})/, "$1-$2-");
}

return birthday;
},
async onScan() {
const _this = this;
let wx = require("weixin-js-sdk");
try {
const { getL0004, ocrInfo } = require("../../static/wxGetData");
let thisUrl = location.href.split("#")[0];
_this.$vux.loading.show({ text: "loading" }); 
let response = await getL0004({ url: thisUrl });
_this.$vux.loading.hide(); 
let res = response;

let options = {
debug: false,
appId: res.appId,
timestamp: res.timeStamp,
nonceStr: res.nonce_str,
signature: res.signature,
jsApiList: ["chooseImage", "uploadImage"]
};

wx.config(options);

wx.error(function(res) {
this.$vux.alert.show({ title: "", content: res.errMsg });
});
wx.ready(function() {
wx.chooseImage({
count: 1, //
sizeType: ["original", "compressed"], // 
sourceType: ["album", "camera"], // 
success: function(res) {
console.log(JSON.stringify(res));
var localIds = res.localIds; 

wx.getLocalImgData({
localId: localIds[0], //ID
success: async function(res) {
var localData = res.localData;
if (localData.indexOf("data:image") != 0) {

localData = "data:image/jpeg;base64," + localData;
}
localData = localData
.replace(/\r|\n/g, "")
.replace("data:image/jgp", "data:image/jpeg");


_this.$vux.loading.show({ text: "" }); 
let reqdata = { imageContent: localData };
let resp = await ocrInfo(reqdata).catch(error => {
_this.$vux.loading.hide(); 

_this.$vux.toast.show({
text: "",
type: "text"
});
});
_this.$vux.loading.hide(); 
let userList = localStorage.getItem("userList");
let List = JSON.parse(userList);
if ( myUtils.listIsExist(List, resp.cardInfo.id, "certNo")    ) {
_this.$vux.toast.show({
text: "",
type: "text"
});
return;
}

_this.$router.replace({
name: "sickCreate2",
params: {
cardInfo: resp.cardInfo,
localId: localIds[0],
wechatCode: _this.wechatCode
}
});
}
});
}
});
});
} catch (error) {
console.log("error", error);
_this.$vux.toast.show({ text: "", type: "text" });
}
}
},

created() {
this.wechatCode = myUtils.getUrlKey("wechatCode");
}
