
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'




export default {
data() {
return {
item
};
},
components: { Group, Cell },
vuex: {},
methods: {},
mounted() {},
created() {
let titem = this.$route.params.item;
if (titem.schedulingSequenceNum == "1") {
titem.schedulingSequenceNum = "";
} else {
titem.schedulingSequenceNum = "";
}
let flag = "";
switch (titem.flag) {
case "0": {
flag = "";
break;
}
case "1": {
flag = "";
break;
}
case "2": {
flag = "";
break;
}
case "3": {
flag = "";
break;
}
}
titem.flag = flag;
if (titem.advance == "1") {
titem.advance = "";
} else {
titem.advance = "";
}
this.item = titem;
}
};



